<template>
<div>    
    <v-runtime-template :template="template"/>
</div>
</template>

<script>
export default {
    name: 'ServerComponet',
    data: () => ({
        template: `
        <app-title>Howdy Yo!</app-title>
        <button @click="send_message('Hello')">Go to crocoland</button>
        `
    }),

    methods: {    
        run_service(params){ // 서비스 호출
            this.$store.dispatch('CALL_RESPONSE', params);
        },
        send_message(msg){ // 메시지 발화
            this.$store.dispatch('SEND_MESSAGE', msg);
        }
    },
};

</script>